'use strict';
import '../style/style.css';
import { TweenMax, CSSPlugin, ScrollToPlugin, Draggable, Elastic, TimelineMax, Linear } from 'gsap/all'; 

let height = window.innerHeight;
let width = window.innerWidth;

init();

var tl0 = new TimelineMax({repeat: -1});
tl0.staggerTo('#bubbleGroup rect, #fizz rect', 2, {
  attr: {
    y: -100,
    height: 0,
    width: 0
  }
}, 0.6)

var tl1 = new TimelineMax({paused: true});
tl1.to("#grassPattern", 100, {
  attr: {
    x: -(width * 800)
  },
  ease: Linear.easeNone
})

var tl2 = new TimelineMax({paused: true});
tl2.to("#subject", 100, {
  attr: {
    x: (width * 800)
  },
  ease: Linear.easeNone
})



addEventListener('keydown', e => {
  //Left arrow 
  if (e.keyCode == 37) { 
    tl1.reverse();
    console.log(document.getElementById('grassPattern').getAttribute('x'));
    moveCharacter();
  }
//Right arrow 
    if (e.keyCode == 39) { 
    tl1.play();
    console.log(document.getElementById('grassPattern').getAttribute('x'));
  }
})

addEventListener('keyup', e => {
  tl1.pause();
})

addEventListener('resize', e => {
  init();
})

function moveCharacter() {
  console.log('hej')
  if(document.getElementById('grassPattern').getAttribute('x') <= 0) {
    console.log('hi')
    tl2.play()
  }
}

function init() {
  height = window.innerHeight;
  width = window.innerWidth;
// TweenMax.to(".heart", 0, {x: 20, y: 20})
// TweenMax.to(".rupie", 0, {x: 120, y: 0})
// TweenMax.to(".luigi", 0, {x: 300, y: 120})
TweenMax.to(".potion", 0, {y: -100})
TweenMax.to(".grass", 0, {y: height - 160, width: width})
TweenMax.to(".sandFloor", 0, {y: height - 100})
TweenMax.to(".subject", 0, {x: width / 2 - 80, y: height - 490})
}